<script>
import { listArticle } from "@/api/qna.js";

export default {
  name: "QuestionList",
  data() {
    return {
      articles: [],
      fields: [
        {
          key: "articleno",
          label: "글번호",
          tdClass: "tdClass",
          sortable: true,
