        { key: "regtime", label: "작성일", tdClass: "tdClass", sortable: true },
        { key: "hit", label: "조회수", tdClass: "tdClass", sortable: true },
      ],
      perPage: 5,
      currentPage: 1,
      options: [
        { value: 5, text: "5개" },
        { value: 10, text: "10개" },
        { value: 20, text: "20개" },
      ],
    };
  },
  created() {
    let param = {
      pg: 1,
      spp: 20,
      key: null,
      word: null,
    };
    listArticle(
      param,
      (response) => {
        this.articles = response.data;
        console.log(response);
      },
      (error) => {
        console.log(error);
      },
    );
  },
  methods: {
    moveWrite() {
      this.$router.push({ name: "questionRegister" });
    },
  },
};
</script>

